<template>
  <div class="menu-list">
    <el-card
      shadow="never">
      <!-- 添加菜单 -->
      <div slot="header">
        <el-button
          @click="handleAddMenu"
        >添加菜单</el-button>
      </div>
      <!-- 菜单表格 -->
      <el-table
        :data="menus"
        border
        style="width:100%">
        <el-table-column
          type="index"
          label="编号"
          align="center"
          width="80" />
        <el-table-column
          prop="name"
          label="菜单名称"
          align="center" />
        <el-table-column
          label="菜单级数"
          align="center">
          <template slot-scope="scope">
            {{ scope.row.level | formatLevel }}
          </template>
        </el-table-column>
        <el-table-column
          prop="icon"
          label="前端图标"
          align="center" />
        <el-table-column
          prop="orderNum"
          label="排序"
          align="center" />
        <el-table-column
          label="操作"
          align="center" >
          <template slot-scope="scope">
            <el-button
              type="text"
              @click="handleEditMenu(scope.row)"
            >编辑</el-button>
            <el-button
              type="text"
              @click="handleDeleteMenu( scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页栏 -->
      <el-pagination
        :page-sizes="[10, 15, 25]"
        :total="menuTotalCount"
        :page-size="menuQuery.size"
        :current-page="menuQuery.current"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handlePageSizeChange"
        @current-change="handleCurrentPageChange"
        :disabled="isLoading"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
import {
  getMenuPages,
  deleteMenu
} from '@/services/menu'

import {
  numToChinese
} from '@/utils/format'

export default {
  name: 'MenuList',
  data () {
    return {
      menus: [],
      menuQuery: {
        size: 10,
        current: 1
      },
      menuTotalCount: 0,
      isLoading: false
    }
  },
  created () {
    this.loadMenus()
  },
  methods: {
    async loadMenus () {
      this.isLoading = true
      const { data } = await getMenuPages(this.menuQuery)
      if (data.code === '000000') {
        this.menus = data.data.records
        this.menuTotalCount = data.data.total
      }
      this.isLoading = false
    },
    handlePageSizeChange (pageSize) {
      this.menuQuery.size = pageSize
      this.menuQuery.current = 1
      this.loadMenus()
    },
    handleCurrentPageChange (current) {
      this.menuQuery.current = current
      this.loadMenus()
    },
    handleAddMenu () {
      this.$router.push({
        name: 'menu-create'
      })
    },
    handleEditMenu (menu) {
      this.$router.push({
        name: 'menu-edit',
        params: {
          menuId: menu.id
        }
      })
    },
    handleDeleteMenu (menu) {
      this.$confirm('确认删除吗？', '删除提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { data } = await deleteMenu(menu.id)
        if (data.code === '000000') {
          this.$message.success('删除成功')
          this.loadMenus()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  },
  filters: {
    formatLevel (level) {
      return `${numToChinese(level + 1)}级`
    }
  }
}
</script>

<style lang="scss" scoped>
.menu-list {
  .el-pagination {
    margin-top: 20px;
  }
}
</style>
